<template>
    <div>
        <base-header type="gradient-success" class="pb-6 pb-8 pt-5 pt-md-8"></base-header>
        <div class="container-fluid mt--7">
            <div class="row">
                <div class="col-xl-12">
                    <card shadow type="secondary">
                        <div slot="header" class="bg-white border-0">
                            <div class="row align-items-center">
                                <div class="col-8">
                                    <h3 class="mb-0">Components</h3>
                                </div>
                                <div class="col-4 text-right">
                                    <a href="#!" class="btn btn-sm btn-primary">Settings</a>
                                </div>
                            </div>
                        </div>
                        <template>
                            <div class="container-fluid">
                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">Radio button</label>
                                        <br/>
                                        <base-radio name="radio0" class="mb-3" v-model="radio.radio1">
                                            Unchecked
                                        </base-radio>

                                        <base-radio name="radio1" class="mb-3" v-model="radio.radio1">
                                            Checked
                                        </base-radio>


                                        <base-radio name="radio2" class="mb-3" v-model="radio.radio2" disabled>
                                            Disabled unchecked
                                        </base-radio>

                                        <base-radio name="radio3" class="mb-3" v-model="radio.radio2" disabled>
                                            Disabled checked
                                        </base-radio>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">Single Select</label>
                                        <br/>
                                        <el-select class="select-danger"
                                                   placeholder="Single Select"
                                                   v-model="selects.simple">
                                            <el-option v-for="option in selects.languages"
                                                       class="select-danger"
                                                       :value="option.value"
                                                       :label="option.label"
                                                       :key="option.label">
                                            </el-option>
                                        </el-select>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">Multiple select </label>
                                        <br/>
                                        <el-select multiple
                                                   class="select-primary"
                                                   v-model="selects.multiple"
                                                   placeholder="Multiple Select">
                                            <el-option v-for="option in selects.languages"
                                                       class="select-primary"
                                                       :value="option.value"
                                                       :label="option.label"
                                                       :key="option.label">
                                            </el-option>
                                        </el-select>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">Auto Complete </label>
                                        <br/>
                                        <el-select
                                                v-model="value"
                                                multiple
                                                filterable
                                                remote
                                                reserve-keyword
                                                placeholder="Nhp k t  gi "
                                                :remote-method="remoteMethod"
                                                :loading="loading">
                                            <el-option
                                                    v-for="item in options"
                                                    :key="item.value"
                                                    :label="item.label"
                                                    :value="item.value">
                                            </el-option>
                                        </el-select>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">DatePicker </label>
                                        <base-input addon-left-icon="ni ni-calendar-grid-58">
                                            <flat-pickr slot-scope="{focus, blur}"
                                                        @on-open="focus"
                                                        @on-close="blur"
                                                        :config="{allowInput: true}"
                                                        class="form-control datepicker"
                                                        v-model="dates.simple">
                                            </flat-pickr>
                                        </base-input>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label">DatePicker Range</label>
                                        <base-input addon-left-icon="ni ni-calendar-grid-58">
                                            <flat-pickr slot-scope="{focus, blur}"
                                                        @on-open="focus"
                                                        @on-close="blur"
                                                        :config="{allowInput: true, mode: 'range'}"
                                                        class="form-control datepicker"
                                                        v-model="dates.range">
                                            </flat-pickr>
                                        </base-input>
                                    </div>
                                </div>

                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label"> Notification</label>
                                        <br/>
                                        <base-button @click="$notify('Simple notification')">Simple</base-button>
                                        <base-button
                                                @click="$notify({type: 'primary', message: 'Primary Notification'})">
                                            Primary
                                        </base-button>
                                        <base-button @click="$notify({type: 'info', message: 'Info Notification'})">Info
                                        </base-button>
                                        <base-button
                                                @click="$notify({type: 'success', message: 'Success Notification'})">
                                            Success
                                        </base-button>
                                        <base-button
                                                @click="$notify({type: 'warning', message: 'Warning Notification'})">
                                            Warning
                                        </base-button>
                                        <base-button @click="$notify({type: 'danger', message: 'Danger Notification'})">
                                            Danger
                                        </base-button>
                                    </div>
                                </div>
                                <div class="row">
                                    <div class="form-group">
                                        <label class="form-control-label"> Notification position</label>
                                        <br/>
                                        <base-button @click="notifyTopLeft">Top left</base-button>
                                        <base-button @click="notifyTopCenter">Top center</base-button>
                                        <base-button @click="notifyTopRight">Top right</base-button>
                                        <base-button @click="notifyBottomLeft">Bottom left</base-button>
                                        <base-button @click="notifyBottomCenter">Bottom center</base-button>
                                        <base-button @click="notifyBottomRight">Bottom right</base-button>
                                    </div>
                                </div>

                            </div>
                        </template>
                    </card>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                radio: {
                    radio1: "radio1",
                    radio2: "radio3"
                },
                dates: {
                    simple: "2018-07-17",
                    range: "2018-07-17 to 2018-07-19"
                },
                selects: {
                    simple: '',
                    languages: [{value: 'Bahasa Indonesia', label: 'Bahasa Indonesia'},
                        {value: 'Bahasa Melayu', label: 'Bahasa Melayu'},
                        {value: 'Catal', label: 'Catal'},
                        {value: 'Dansk', label: 'Dansk'},
                        {value: 'Deutsch', label: 'Deutsch'},
                        {value: 'English', label: 'English'},
                        {value: 'Espaol', label: 'Espaol'},
                        {value: 'E', label: 'E'},
                        {value: 'Franais', label: 'Franais'},
                        {value: 'Italiano', label: 'Italiano'},
                        {value: 'Magyar', label: 'Magyar'},
                        {value: 'Nederlands', label: 'Nederlands'},
                        {value: 'Norsk', label: 'Norsk'},
                        {value: 'Polski', label: 'Polski'},
                        {value: 'Portugus', label: 'Portugus'},
                        {value: 'Suomi', label: 'Suomi'},
                        {value: 'Svenska', label: 'Svenska'},
                        {value: 'Trke', label: 'Trke'},
                        {value: 'slenska', label: 'slenska'},
                        {value: 'etina', label: 'etina'},
                        {value: '', label: ''},
                        {value: '', label: ''},
                        {value: ' ()', label: ' ()'},
                        {value: 'W"> ()', label: 'W"> ()'},
                        {value: '', label: ''},
                        {value: '', label: ''}],
                    multiple: 'ARS'
                },
                options: [],
                value: [],
                list: [],
                loading: false,
                states: ["Alabama", "Alaska", "Arizona",
                    "Arkansas", "California", "Colorado",
                    "Connecticut", "Delaware", "Florida",
                    "Georgia", "Hawaii", "Idaho", "Illinois",
                    "Indiana", "Iowa", "Kansas", "Kentucky",
                    "Louisiana", "Maine", "Maryland",
                    "Massachusetts", "Michigan", "Minnesota",
                    "Mississippi", "Missouri", "Montana",
                    "Nebraska", "Nevada", "New Hampshire",
                    "New Jersey", "New Mexico", "New York",
                    "North Carolina", "North Dakota", "Ohio",
                    "Oklahoma", "Oregon", "Pennsylvania",
                    "Rhode Island", "South Carolina",
                    "South Dakota", "Tennessee", "Texas",
                    "Utah", "Vermont", "Virginia",
                    "Washington", "West Virginia", "Wisconsin",
                    "Wyoming"]
            }
        },
        mounted() {
            this.list = this.states.map(item => {
                return {value: `value:${item}`, label: `label:${item}`};
            });
        },
        methods: {
            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.options = this.list.filter(item => {
                            return item.label.toLowerCase()
                                .indexOf(query.toLowerCase()) > -1;
                        });
                    }, 200);
                } else {
                    this.options = [];
                }
            },
            notifyTopRight() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'right', message: 'Top right'});
            },
            notifyTopLeft() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'left', message: 'Top left'});
            },
            notifyTopCenter() {
                this.$notify({verticalAlign: 'top', horizontalAlign: 'center', message: 'Top center'});
            },
            notifyBottomLeft() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'left', message: 'Bottom left'});
            },
            notifyBottomCenter() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'center', message: 'Bottom center'});
            },
            notifyBottomRight() {
                this.$notify({verticalAlign: 'bottom', horizontalAlign: 'right', message: 'Bottom center'});
            }
        }
    };
</script>
<style></style>
